@import url("https://fonts.googleapis.com/css?family=Raleway:100,300,400,500,700,900");

*{
  margin:0;
  padding:0;
  font-family: 'Raleway', sans-serif;
}

nav{
  width:100%;
  background-color: rgb(255, 255, 255);
  position: fixed;
  z-index:100;
  display: flex;
  flex-direction: row;
  margin:2rem;
  padding:0.7rem;
  justify-content: flex-start;
  align-items: center;
}

#logo{
  text-decoration: none;
  color:#3f2952 ;
  font: 600 1.2rem "Raleway";
  display:flex;
}

nav div{
  display: flex;
  flex-direction: row;
  text-decoration: none;
  width:100%;
  justify-content: flex-end;
  align-items: center;
  margin-right: 10rem;
}

nav div a{
  text-decoration: none;
  font:400 1rem "Raleway";
  padding:1rem;
  color:#3f2952 ;
}

nav div a:hover{
  text-decoration: underline;
}

nav button{
  text-decoration: none;
  font:400 1rem "Raleway";
  color:#3f2952 ;
  background-color: white;
  padding:0.5rem;
  padding-left:1.5rem;
  padding-right:1.5rem;
  margin-left: 1.5rem;
  border-color: #3f2952;
  border-radius: 20rem;
  transition: all 0.5s;
}

nav button:hover{
  background-color: #3f2952;
  color:white;
}